import {closeModalWindow, showModalWindow} from "./modal"; // -      modal
import {postData} from "../services/services";

function forms(formSelector, modalTimer) {
    //5.   (POST)
    const forms = document.querySelectorAll(formSelector); // 
    const message = { //     
        loading: 'icons/spinner.svg', //  . 
        success: '!     ',
        failure: '-   ...'
    };

    forms.forEach(item => { //  - postData   -
        bindPostData(item);
    });


    function bindPostData(form) {
        form.addEventListener('submit', (e) => {
            e.preventDefault(); // .  , .   
                clearInterval(modalTimer);//       

            const statusMessage = document.createElement('img'); //      
            statusMessage.src = message.loading; //    src  html-
            statusMessage.style.cssText = `
                display: block;
                margin: 0 auto`; //-e cssText   
            form.append(statusMessage); //    form
            form.insertAdjacentElement('afterend', statusMessage); // : ,  (statusMessage-)
            
            const formData = new FormData(form); //  FormData,    -  ,    -.  form, -   .  html       name=""
            const json = JSON.stringify(Object.fromEntries(formData.entries())); // fromData    json

            postData('http://localhost:3000/requests', json)// -  
            .then(data => {
                console.log(data);
                showThanksModal(message.success); //   
                statusMessage.remove(); //    
            })
            .catch(() => {
                showThanksModal(message.failure);
            })
            .finally(() => {
                form.reset();//    
            });     
        });
    }
    //-     
    function showThanksModal(message) {
        const previousModalDialog = document.querySelector('.modal__dialog');
        previousModalDialog.classList.add('hide'); // .  ,    
        showModalWindow('.modal', modalTimer); //     

        const thanksModal = document.createElement('div'); //    
            thanksModal.classList.add('modal__dialog'); //   modal__dialog, .  modal__dialog  
            thanksModal.innerHTML = `
                <div class="modal__content">
                    <div class="modal__close" data-close></div>
                    <div class="modal__title">${message}</div>
                </div>
            `; // . message -   - showThanksModal,      const message

            document.querySelector('.modal').append(thanksModal); //     
            setTimeout(() => { //  / 4          
                thanksModal.remove(); //   ,    
                previousModalDialog.classList.add('show'); //   
                previousModalDialog.classList.remove('hide');
                closeModalWindow('.modal'); //    . '.modal'-   - 
            }, 4000);
    }

    //   JSON
    fetch('http://localhost:3000/menu')
        .then(data => data.json())
        .then(res => console.log(res));
}
export default forms;