//-   
function showModalWindow (modalSelector, modalTimer) { //    script.js
    const modalWindow = document.querySelector(modalSelector);
    
    modalWindow.classList.add('show');
    modalWindow.classList.remove('hide');
    document.body.style.overflow = 'hidden';// 
    if (modalTimer) { //  modalTimer,    
        clearInterval(modalTimer);//    ,     
    }
}
// -  
function closeModalWindow (modalSelector) { //    script.js
    const modalWindow = document.querySelector(modalSelector);
    
    modalWindow.classList.add('hide');
    modalWindow.classList.remove('show');
    document.body.style.overflow = '';//  ,   -      
    // modalDialog.classList.add('show');
    // modalDialog.classList.remove('hide');
}

function modal(triggerSelector, modalSelector, modalTimer) {
    //3. Modal window
    const btnContact = document.querySelectorAll(triggerSelector),
          modalWindow = document.querySelector(modalSelector);
        //   modalDialog = document.querySelector('.modal__dialog');

    //      
    btnContact.forEach(item => {
    item.addEventListener('click', () => showModalWindow(modalSelector, modalTimer)); // -   showModalWindow. 
    //  : item.addEventListener('click', showModalWindow); -   -, . -    .  showModalWindow(modalSelector)   , .. -     .     -
    });



    //            
    modalWindow.addEventListener('click', (e) => {
    if(e.target === modalWindow || e.target.getAttribute('data-close') == "") { //     modal   (). data-close   , ..     (  "    .    ,   getAttribute()         -  true,    data-close  HTML          "" ,          
        closeModalWindow(modalSelector);  
    }
    });

    
    //      Esc
    document.addEventListener('keydown', (e) => {
    if (e.code === "Escape" && modalWindow.classList.contains('show')) {//   Esc    , 
        closeModalWindow(modalSelector);  
    }
    });



    //-      
    function showModalByScroll() {
    if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight - 1) { //    +   >=   (.  ),   
        showModalWindow(modalSelector, modalTimer);
        window.removeEventListener('scroll', showModalByScroll); //   - -  
    }
    }
    window.addEventListener('scroll', showModalByScroll); //{once:true}   , ..    .   
}
export default modal;
export {closeModalWindow}; // -  
export {showModalWindow}; // -  