//-     
const postData = async (url, data) => {//url , data- 
    const res = await fetch(url, { //   fetch( await). Fetch  
        method: 'POST', // 
        headers: { // 
            'Content-type': 'application/json'
    },
        body: data
    });
    return await res.json(); //   json,    fetch( await)
};

//- GET-
const getResoure = async (url) => { //url ,  -  
    const res = await fetch(url);
    if (!res.ok) { //     
        throw new Error(`Could not fetch ${url}, status; ${res.status}`); //    (throw)   (new Error)    'Could not fetch...'
    }
    return await res.json(); //   json,    fetch( await)
};


export {postData};
export {getResoure};