function timer(id, deadline) { //id -  
    // 2. Timer
    // -  ,   ..
    function getTimeRemaining(endtime) {
        let t = Date.parse(endtime) - Date.parse(new Date()), //     . 
            days = Math.floor(t / (1000 * 60 * 60 * 24)), //  - . Math.floor() -   . 
            hours = Math.floor((t / (1000 * 60 * 60) % 24)), //     %
            minutes = Math.floor((t / 1000 / 60) % 60), //  
            seconds = Math.floor((t / 1000) % 60); //  
            if(t <= 0) { //     00:00   
                days = 0;
                hours = 0;
                minutes = 0;
                seconds = 0;
                
            }

        return { //     
            'total' : t, //  - ,      .  (   - updateClock())
            'days' : days, //    
            'hours' : hours,
            'minutes' : minutes,
            'seconds' : seconds
        }; 
    }
    //-   0  1 - 9
    function getZero(num) { 
        if (num >=0 && num < 10) {
            return `0${num}`;
        } else {
            return num;
        }
    }
    // -     
    function setClock (selector, endtime) { //selector-. ,  .   '.timer'
        const timer = document.querySelector(selector), // selector    - setClock 
            days = timer.querySelector('#days'), //  id   html
            hours = timer.querySelector('#hours'),
            minutes = timer.querySelector('#minutes'),
            seconds = timer.querySelector('#seconds'),
            timeInterval = setInterval(updateClock, 1000); //     , - updateClock(), -      1000, .. 1 

            updateClock(); //  - ,    .   1000 . - 

        // -          
        function updateClock() {
            const t = getTimeRemaining(endtime); //  t, ,   - - getTimeRemaining(), endtime-, -  

            days.innerHTML = getZero(t.days); //  id ='days'(#days)   innerHTML    t,   
            hours.innerHTML = getZero(t.hours); //  
            minutes.innerHTML = getZero(t.minutes); // - getZero   0  1-9
            seconds.innerHTML = getZero(t.seconds);

            if (t.total <= 0) {
                clearInterval(timeInterval); //  total    ,     clearInterval
            }
        }
    }
    setClock (id, deadline); // -

}
export default timer;