import { getResoure } from "../services/services";

function cards() {
    //4.     
    class MenuCard {
        constructor (src, alt, title, descr, price, parentSelector, ...classes) {//classes- rest-, .        ( .menu__item) 
            this.src = src; //  
            this.alt = alt; //alt 
            this.title = title; //
            this.descr = descr; // 
            this.price = price; //  
            this.classes = classes; //,  menu_item -  
            this.parent = document.querySelector(parentSelector); //  
            this.transfer = 36.77; //     
            this.changeToUAH(); //  -    price
        }
        changeToUAH() { // - 
            this.price = +Math.floor(this.price * this.transfer);
        }
        render() { // -   
            const elementMenu = document.createElement('div'); //    div
            if (this.classes.length == 0) { //     ,    0(.  ,   .menu__item)
                this.classes = 'menu__item';
                elementMenu.classList.add(this.classes);
            } else {
                this.classes.forEach(className => elementMenu.classList.add(className));
            }// ,  elementMenu    / add
            elementMenu.innerHTML = ` 
                <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.title}</h3>
                <div class="menu__item-descr">${this.descr}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">:</div>
                    <div class="menu__item-total"><span>${this.price}</span> /</div>
                </div>`; // 
            this.parent.append(elementMenu); // ElementMenu    parent
        }    
    }

    //     
    getResoure('http://localhost:3000/menu') 
        .then(data => { //data -     localhost:3000
            data.forEach(({img, altimg, title, descr, price}) => {//  {}-    data  
                new MenuCard(img, altimg, title, descr, price, '.menu .container').render(); //   Menucard,        data   (.render)  
            });
        });
}

export default cards;